@import url(https://fonts.googleapis.com/css2?family=Comfortaa&family=Nova+Flat&family=Philosopher:ital@1&family=Ubuntu&family=Roboto+Condensed&family=Satisfy&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}
body{
	background-image: url(/christmas-for-everyone/static/media/Trianglify.d8c4e110.png);
	padding: 0;
	margin: 0;
	height: 100vh;
	width:100vw;
  display:flex;
  text-align: center;
	justify-content: center;
}


.loader{
	perspective: 700px;
}

.loader>span{
	font-size: 130px;
	font-family: "franklin gothic medium",sans-serif;
	display: inline-block;
	-webkit-animation:flip 2.6s infinite linear;
	        animation:flip 2.6s infinite linear;
	transform-origin:0 70%;
	transform-style:preserve-3d;
	-webkit-transform-style:preserve-3d;
}

@-webkit-keyframes flip{
	35%{
		transform: rotateX(360deg);
	}
	100%{
		transform: rotatex(360deg);
	}
}

@keyframes flip{
	35%{
		transform: rotateX(360deg);
	}
	100%{
		transform: rotatex(360deg);
	}
}


.loader>span:nth-child(even){
	color:white;

}

.loader>span:nth-child(2){
	-webkit-animation-delay: 0.3s;
	        animation-delay: 0.3s;
}

.loader>span:nth-child(3){
	-webkit-animation-delay: 0.6s;
	        animation-delay: 0.6s;
}

.loader>span:nth-child(4){
	-webkit-animation-delay: 0.9s;
	        animation-delay: 0.9s;
}

.loader>span:nth-child(5){
	-webkit-animation-delay: 1.2s;
	        animation-delay: 1.2s;
}

.loader>span:nth-child(6){
	-webkit-animation-delay: 1.5s;
	        animation-delay: 1.5s
}

.loader>span:nth-child(7){
	-webkit-animation-delay: 1.8s;
	        animation-delay: 1.8s
}

.typewriting {
  font-family: 'Philosopher', sans-serif;
  overflow: hidden;
  border-right: .15em solid orange; 
  white-space: nowrap;
  margin: auto; 
  letter-spacing: .15em; 
  -webkit-animation: 
    typing 3.5s steps(40, end),
    blink-caret .75s step-end infinite; 
          animation: 
    typing 3.5s steps(40, end),
    blink-caret .75s step-end infinite;
}

/* The typing effect */
@-webkit-keyframes typing {
  from { width: 0 }
  to { width: 100% }
}
@keyframes typing {
  from { width: 0 }
  to { width: 100% }
}

/* The typewriter cursor effect */
@-webkit-keyframes blink-caret {
  from, to { border-color: transparent }
  50% { border-color: orange; }
}
@keyframes blink-caret {
  from, to { border-color: transparent }
  50% { border-color: orange; }
}

.caption{
  font-family: 'Satisfy', cursive;
  -webkit-animation: fade-in 5s linear;
          animation: fade-in 5s linear;
}

@-webkit-keyframes fade-in{
  from{
    opacity: 0;
  }
  to{
    opacity: 1;
  }
}

@keyframes fade-in{
  from{
    opacity: 0;
  }
  to{
    opacity: 1;
  }
}

.img-left{
  -webkit-animation: slide 2s linear;
          animation: slide 2s linear;
}

@-webkit-keyframes slide{
  from{
    transform: translateX(-50%);
    opacity: 0;
  }
  to{
    transform: translateX(0%);
    opacity: 1;
  }
}

@keyframes slide{
  from{
    transform: translateX(-50%);
    opacity: 0;
  }
  to{
    transform: translateX(0%);
    opacity: 1;
  }
}

.img-right{
  -webkit-animation: slide-r 2s linear;
          animation: slide-r 2s linear;
}

@-webkit-keyframes slide-r{
  from{
    transform: translateX(50%);
    opacity: 0;
  }
  to{
    transform: translateX(0%);
    opacity: 1;
  }
}

@keyframes slide-r{
  from{
    transform: translateX(50%);
    opacity: 0;
  }
  to{
    transform: translateX(0%);
    opacity: 1;
  }
}

.content{
  -webkit-animation: fade-in 3s linear;
          animation: fade-in 3s linear;
}

@-webkit-keyframes twist{
  from{
    transform: rotate(60);
  }
  to{
    transform: rotate(0); 
  }
}

@keyframes twist{
  from{
    transform: rotate(60);
  }
  to{
    transform: rotate(0); 
  }
}

.tooltip {
  position: relative;
  display: inline-block;
  border-bottom: 1px dotted black; /* If you want dots under the hoverable text */
}

/* Tooltip text */
.tooltip .tooltiptext {
  visibility: hidden;
  width: 120px;
  background-color: black;
  color: #fff;
  text-align: center;
  padding: 5px 0;
  border-radius: 6px;
 
  /* Position the tooltip text - see examples below! */
  position: absolute;
  z-index: 1;
}

/* Show the tooltip text when you mouse over the tooltip container */
.tooltip:hover .tooltiptext {
  visibility: visible;
}
